/* src/components/Login/Login.css */

body {
    margin: 0;
    font-family: 'Arial', sans-serif;
    background-image: url('https://source.unsplash.com/random/1600x900/?nature'); /* Background image */
    background-size: cover; /* Cover the entire background */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Full height of the viewport */
}

.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-box {
    background: rgba(255, 255, 255, 0.9); /* Semi-transparent white background */
    padding: 30px;
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2); /* Soft shadow */
    max-width: 400px;
    width: 100%; /* Full width of the box */
}

h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px; /* Spacing below the heading */
}

.input-group {
    margin-bottom: 15px; /* Spacing between input groups */
}

label {
    display: block;
    margin-bottom: 5px; /* Spacing below labels */
    color: #666; /* Label color */
}

input {
    width: 100%;
    padding: 12px; /* Increased padding */
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px; /* Increase font size */
    transition: border-color 0.3s; /* Smooth transition */
}

input:focus {
    border-color: #007bff; /* Blue border on focus */
    outline: none; /* Remove outline */
}

button {
    width: 100%;
    padding: 12px; /* Increased padding */
    background-color: #007bff; /* Primary button color */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px; /* Increase font size */
    transition: background-color 0.3s; /* Smooth transition */
}

button:hover {
    background-color: #0056b3; /* Darker blue on hover */
}

.error {
    color: red;
    text-align: center; /* Center the error message */
}

.signup-link {
    text-align: center;
    margin-top: 15px; /* Space above the link */
}

.signup-link a {
    color: #007bff; /* Link color */
    text-decoration: none; /* Remove underline */
}

.signup-link a:hover {
    text-decoration: underline; /* Underline on hover */
}